import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PeritosRoutingModule } from './peritos-routing.module';
import { PeritosComponent } from './peritos.component';
import { NgxPermissionsModule } from 'ngx-permissions';
import { BoxModule, BoxSmallModule as MkBoxSmallModule  } from 'angular-admin-lte';
import { NgZorroAntdModule, NZ_ICONS, NzSelectModule, NzModalModule, NzDropDownModule } from 'ng-zorro-antd';
import { IconDefinition } from '@ant-design/icons-angular';
import * as AllIcons from '@ant-design/icons-angular/icons';

const antDesignIcons = AllIcons as {
  [key: string]: IconDefinition;
};

const icons: IconDefinition[] = Object.keys(antDesignIcons).map(key => antDesignIcons[key])

import 'rxjs';

@NgModule({
  declarations: [PeritosComponent],
  imports: [
    CommonModule,
    PeritosRoutingModule,
    NzDropDownModule,
    NgZorroAntdModule,
    NzSelectModule,
    NzModalModule,
    BoxModule,
    MkBoxSmallModule,
    NgxPermissionsModule.forChild(),
    ReactiveFormsModule,
    FormsModule,
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  providers: [DatePipe, { provide: NZ_ICONS, useValue: icons }]
})
export class PeritosModule { }
