  onSubmitEditIndicio() {
    this.submittedEditIndicio = true;

    if (this.indicioEditForm.invalid) {
      return;
    }

    this.isConfirmLoadingEditIndicio = true;

    let indicio = new Indicio(
      this.indicioXPeritoDataSelected.id_indicio,
      this.indicioXPeritoDataSelected.identificacion,
      this.indicioEditForm.get('descripcion').value,
      this.indicioXPeritoDataSelected.fecha,
      this.indicioXPeritoDataSelected.hora,
      this.indicioXPeritoDataSelected.documentacion,
      this.indicioXPeritoDataSelected.recoleccion,
      this.indicioXPeritoDataSelected.embalaje,
      this.indicioXPeritoDataSelected.latitud,
      this.indicioXPeritoDataSelected.longitud,
      this.indicioEditForm.get('lugar').value,
      this.indicioXPeritoDataSelected.foto1,
      this.indicioXPeritoDataSelected.naturaleza,
      this.indicioXPeritoDataSelected.nombreUsuario,
      this.indicioXPeritoDataSelected.usuario,
      this.indicioXPeritoDataSelected.smart_tag,
      this.indicioXPeritoDataSelected.foto2,
      this.indicioXPeritoDataSelected.nuc,
      this.indicioXPeritoDataSelected.escrito,
      this.indicioXPeritoDataSelected.fotografico,
      this.indicioXPeritoDataSelected.croquis,
      this.indicioXPeritoDataSelected.otro,
      this.indicioXPeritoDataSelected.especifique,
      this.indicioXPeritoDataSelected.entregado,
      this.indicioXPeritoDataSelected.estado
    );

    console.log(indicio);

    this.indiciosService.updateIndicio(indicio).subscribe(result => {
      this.isConfirmLoadingEditIndicio = false;
      this.isVisibleEditIndicio = false;
      this.toastr.success('Campos actualizados!');

      setTimeout(() => {      
        this.Logs(
          'indicios', 
          'Edicion del indicio ' + this.indicioXPeritoDataSelected.identificacion, 
          this.indicioXPeritoDataSelected.id_indicio, 
          'Actualizacion', 
          'Realizo cambios en el campo de descripcion de "' + this.indicioXPeritoDataSelected.descripcion + '" a "' + this.indicioEditForm.get('descripcion').value + '" y realizo cambios en lugar de "' + this.indicioXPeritoDataSelected.lugar + '" a "' + this.indicioEditForm.get('lugar').value + '".'
        );
      }, 1000);

      // this.usuariosService.getUsersApp().subscribe(request => {
      //   this.usuarios = request;
      //   this.isLoading = false;
      // },
      // error => {
      //   console.log(error);
      // });
    }, err => {
      console.log(err);
      this.isConfirmLoadingEditIndicio = false;

      this.toastr.error('Hubo un error al editar el indicio');
    });

    this.submittedEditIndicio = false;
  }

